.root {
    margin: 30px;
}

.content {
    width: 1000px;
    margin: 0 auto;
}

.timeline {
    width: 480px;
}

.items {
    margin-top:20px;
    display: flex;
    justify-content: space-evenly;
}

.postForm {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    overflow: hidden;
    border: 1px solid #ebe9e6;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}

.avatar {
    display: block;
    width: 40px;
    height: 40px;
    border-radius: 50%;
}